//App.js
import styled from "styled-components";
import {
	FacebookShareButton,
	FacebookIcon,
	TwitterIcon,
	TwitterShareButton,
} from "react-share";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { useScript } from "./Hooks";
import { useEffect } from "react";
import kakaoLogo from "./images/kakao.png";

const Popup = styled.div`
	width: 100vw;
	height: 65vh;
	padding-top: 35vh;
	background-color: rgba(0,0,0,0.6);
	position : fixed;
	z-index : 1;
	touch-action : none;
`

//    
const FlexContainer = styled.div`
	width: 80%;
	max-width: 300px;
	margin: auto;
	border-radius: 30px;
	background-color: #fffaf0;

	display: flex;
	flex-direction: column;
	align-items: center;
`;

//   
const GridContainer = styled.div`
	display: grid;
	grid-template-columns: repeat(4, 48px);
	grid-column-gap: 8px;
	justify-content: center;
	align-items: center;
	margin-bottom: 16px;
`;

// Style    
const URLShareButton = styled.button`
	width: 48px;
	height: 48px;
	color: white;
	border-radius: 24px;
	border: 0px;
	font-weight: 800;
	font-size: 18px;
	cursor: pointer;
	background-color: #7362ff;
	&:hover {
		background-color: #a99fee;
	}
`;
const KakaoShareButton = styled.a`
	cursor: pointer;
`;

const KakaoIcon = styled.img`
	width: 48px;
	height: 48px;
	border-radius: 24px;
`;

const BackBtn = styled.div`

	cursor: pointer;
	color: #fffaf0;
	background-color: #7e581f;
	
	padding: 2%;
	padding-left: 5%;
	padding-right: 5%;

	border-radius: 20px;

	margin-bottom:	5%;
`;


function SocialShare({setPopupOpen}) {
    // window   url 
	const currentUrl = window.location.href;
	//kakao SDK import 
	const status = useScript("https://developers.kakao.com/sdk/js/kakao.js");

	// kakao sdk 
	// status   , status ready   .
	useEffect(() => {
	  if (status === "ready" && window.Kakao) {
		  //  initialization 
		  if (!window.Kakao.isInitialized()) {
			  //  step   javascript key   initialize
			  window.Kakao.init("687249173015ebd36dbbab09efdc58b6");
		  }
	  }
	}, [status]);

	const handleKakaoButton = () => {
		window.Kakao.Link.sendDefault({
			objectType: 'feed',
			content: {
				title: '    ',
				description: '   !',
				imageUrl:
					'https://github.com/zmin9/mallang-sulddeok/blob/main/public/home_image.jpg?raw=true',
				link: {
					mobileWebUrl: currentUrl,
					webUrl: currentUrl,
				},
			},
			buttons: [
				{
					title: ' ',
					link: {
						mobileWebUrl: currentUrl,
						webUrl: currentUrl,
					},
				},
				{
					title: ' ',
					link: {
						mobileWebUrl: 'https://zmin9.github.io/mallang-sulddeok/',
						webUrl: 'https://zmin9.github.io/mallang-sulddeok/',
					},
				},
			],
		});
	};


	return (
		<Popup>
			<FlexContainer>
				<h2 style={{color:'#1f170b'}}></h2>
				<GridContainer>
					<FacebookShareButton url={currentUrl}>
						<FacebookIcon size={48} round={true} borderRadius={24}></FacebookIcon>
					</FacebookShareButton>
					<TwitterShareButton url={currentUrl}>
						<TwitterIcon size={48} round={true} borderRadius={24}></TwitterIcon>
					</TwitterShareButton>
					<CopyToClipboard text={currentUrl}>
						<URLShareButton>URL</URLShareButton>
					</CopyToClipboard>
					<KakaoShareButton onClick={handleKakaoButton}>
						<KakaoIcon src={kakaoLogo}></KakaoIcon>
					</KakaoShareButton>
				</GridContainer>
				<BackBtn onClick={()=>(setPopupOpen(false))}></BackBtn>
			</FlexContainer>
		</Popup>
	);
}


export default SocialShare;
